import React, { Component } from 'react';
import TaskItem from './TaskItem';
export default class TaskList extends Component {
    constructor(props){
        super(props)
        this.state = {
            search:'',
            status:-1
        }

    }
    delete=(id)=>{
        this.props.delete(id);
    }
    update=(id)=>{
        this.props.update(id);
    }
    changeValue=(event)=>{
        let target = event.target;
        let name = target.name;
        let value = target.value;
        this.setState({
            [name]:value
        },()=>{
            this.props.filter(this.state.search,this.state.status);
        });
    }
    render() {
        let element = this.props.tasks.map((item,index) => {
            return(
                <TaskItem key={index} update={this.update} delete={this.delete} index={index+1} name={item.name} status={item.status} id={item.id}/>
            )
        });
        return (
            <table className="table table-bordered">
                <thead>
                    <tr>
                        <th scope="col">STT</th>
                        <th scope="col">Tên</th>
                        <th scope="col">Trạng thái</th>
                        <th scope="col">Hoạt động</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th scope="row"></th>
                        <td><input
                            type="text"
                            className="form-control"
                            name="search"
                            onChange={this.changeValue}
                        /></td>
                        <td>
                            <select className="form-control" onChange={this.changeValue} name="status">
                                <option value={-1}>Tất cả</option>
                                <option value={0}>Ẩn</option>
                                <option value={1}>Kích hoạt</option>
                            </select>
                        </td>
                        <td></td>
                    </tr>
                    {element}
                </tbody>
            </table>
        )
    }
}