import React, { Component } from 'react';
export default class TaskItem extends Component {
    update = (id)=>{
        this.props.update(id);
    }
    delete = (id)=>{
        this.props.delete(id);
    }
    render() {
        return (
            <tr>
                <th scope="row">{this.props.index}</th>
                <td>{this.props.name}</td>
                <td><span className={this.props.status?'trangthai kichhoat':'trangthai an'}>{this.props.status?'Kích hoạt':'Ẩn'}</span></td>
                <td>
                    <div className="center">
                        <button type="button" onClick={() =>{this.update(this.props.id)}} className="btn btn-warning"><i className="fas fa-pen"></i>&nbsp;Sửa</button>&nbsp;
                        <button type="button" onClick={() =>{this.delete(this.props.id)}} className="btn btn-danger"><i className="fas fa-trash-alt"></i>&nbsp;Xóa</button>
                    </div>
                </td>
            </tr>
        )
    }
}