import React, { Component } from 'react';
import './App.css';
import TaskForm from './components/TaskForm';
import Control from './components/Control';
import TaskList from './components/TaskList';
import uuidv4 from 'uuid';
import _ from 'lodash';

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      hidden:true,
      tasks:[],
      tasksEdit:null,
      filter:{
        search:'',
        status:-1
      },
      keyword:'',
      sort:{
        by:'name',
        value:1
      }
    }
  }
  hide = () =>{
    this.setState({
      hidden:true,
      tasksEdit:null
    });
  }
  show = () =>{
    this.setState({
      hidden:false
    });
  }
  componentWillMount(){
    if(localStorage && localStorage.getItem('tasks')){
      var tasks = JSON.parse(localStorage.getItem('tasks'));
      this.setState({
        tasks:tasks
      });
    }
      
  }
  save = (data) =>{
    if(data.id !== ''){
      console.log(data)
      let tasks= JSON.parse(localStorage.getItem('tasks'));
        tasks = tasks?tasks:[];
        let index = this.findById(data.id);
        tasks[index]= {
          id:data.id,
          name:data.txtName,
          status:data.txtStatus
        }
        this.setState({
          tasks:tasks
        });
  
        localStorage.setItem('tasks',JSON.stringify(tasks));
    }else{
      if(localStorage){
        let tasks= JSON.parse(localStorage.getItem('tasks'));
        tasks = tasks?tasks:[];
        tasks.push({
          id:uuidv4(),
          name:data.txtName,
          status:data.txtStatus
        })
        this.setState({
          tasks:tasks
        });
        localStorage.setItem('tasks',JSON.stringify(tasks));
      }
    }
  }
  delete =(id)=>{
    // var index = this.findById(id);
    let index = _.findIndex(this.state.tasks,(task)=>{
      return task.id === id;
    });
    let tasks = this.state.tasks;
    if(index !== -1){
      tasks.splice(index,1);
      this.setState({
        tasks:tasks                          
      });
      localStorage.setItem('tasks',JSON.stringify(tasks));
      this.hide();
    
    }
  }
  findById =(id)=>{
    let result = -1;
    this.state.tasks.forEach((item,index) =>{
      if(item.id === id){
        result = index;
      }
    });
    return result;
  }
  update=(id)=>{
    let data = this.findData(id);
    if(data){
      this.setState({
        tasksEdit:data
      });
    }
    this.show();

  }
  findData=(id)=>{
    let data ;
    this.state.tasks.forEach((item) =>{
      if(item.id === id){
        data = item;
      }
    });
    return data;
  }
  filter =(search,status)=>{
    this.setState({
      filter:{
        search:search.toLowerCase(),
        status:+status
      }
    })
  }
  onSearch = (keyword)=>{
    this.setState({
      keyword:keyword
    });
  }
  sortA_Z=(name,value)=>{
    this.setState({
      sort:{
        by:name,
        value:+value
      }
    });
  }
  sortZ_A=(name,value)=>{
    this.setState({
      sort:{
        by:name,
        value:+value
      }
    });
  }
  sortActive=(status,value)=>{
    this.setState({
      sort:{
        by:status,
        value:+value
      }
    });
  }
  sortHidden=(status,value)=>{
    this.setState({
      sort:{
        by:status,
        value:+value
      }
    });
  }
  render(){
    let filter = this.state.filter;
    let tasks = this.state.tasks;
    let keyword = this.state.keyword;
    let sort = this.state.sort;
    if(filter){
      if(filter.search){
        tasks = tasks.filter((task)=>{
          return task.name.toLowerCase().indexOf(filter.search) !== -1;
        });
      }
      tasks = tasks.filter((task)=>{
        if(filter.status === -1){
          return task;
        }else{
          return task.status === (filter.status === 1?true:false)
        }
      });
    }
    if(keyword){
      tasks = tasks.filter((task)=>{
        return task.name.toLowerCase().indexOf(keyword.toLowerCase()) !== -1;
      })
    }
    if(sort.by === 'name'){
      tasks.sort((a,b)=>{
        if(a.name > b.name) return sort.value;
        else if(a.name < b.name) return -sort.value;
        else return 0;
      });
    }else{
      tasks.sort((a,b)=>{
        if(a.status > b.status) return -sort.value;
        else if(a.status < b.status) return sort.value;
        else return 0;
      });
    }
    return(
      <div className="container">
        <h2 className="center">Quản Lý Công Việc</h2>
        <div className="row">
          <TaskForm update={this.state.tasksEdit} hidden={this.state.hidden} hide={this.hide} show={this.show} save={this.save}/>
          <div className={this.state.hidden?'col-12':'col-8'}>
            <div className="mb-3">
              <button onClick={this.show} type="button" className="btn btn-primary"><i className="fas fa-plus"></i>&nbsp;Thêm công việc</button>
            </div>
            
            <Control 
              onSearch={this.onSearch} 
              sortA_Z={this.sortA_Z}
              sortZ_A={this.sortZ_A}
              sortActive={this.sortActive}
              sortHidden={this.sortHidden}
            />
            
            <div className="center">
              <TaskList filter={this.filter} tasks={tasks} update={this.update} delete={this.delete}/>
            </div>
          </div>
        </div>
      </div>
      
    )
  }
}
export default App;
