import React, { Component } from 'react';


class TaskForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      hidden: this.props.hidden,
      id: '',
      txtName: '',
      txtStatus: true

    };
  }
  hidden = () => {
    this.setState({
      hidden: true,
      id:'',
      txtName:'',
      txtStatus:false,
      update:null
    });
    this.props.hide();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.hidden !== this.props.hidden) {
      this.setState({
        hidden: nextProps.hidden,
        id:'',
        txtName:'',
        txtStatus:false
      });
    }
    if(nextProps.update && nextProps.update !== this.props.update){
      this.setState({
        id:nextProps.update.id,
        txtName:nextProps.update.name,
        txtStatus:nextProps.update.status
      })
    }
  }
  changeFrom = (event) => {
    let target = event.target;
    let name = target.name;
    let value = target.value;
    if (name === 'txtStatus') {
      value = target.value === 'true' ? true : false
    }
    this.setState({
      [name]: value
    })
  }
  save = () => {
    if (this.state.txtName !== '') {
      this.props.save(this.state);
      this.clean();
      this.hidden();
    }
  }
  clean = () => {
    this.setState({
      id: '',
      txtName: '',
      txtStatus: true
    })
  }
  
  render() {
    return (
      <div className="card col-4" hidden={this.state.hidden}>
        <div className="card-header">
          {this.state.id === '' ? 'Thêm công việc' : 'Sửa công việc'}
          <button className="btn-icon" onClick={this.hidden}><i className="fas fa-times-circle right"></i></button>
        </div>
        <div className="card-body">
          <div className="form-group">
            <label>Tên:</label>
            <input
              type="text"
              name="txtName"
              value={this.state.txtName}
              onChange={this.changeFrom}
              className="form-control"
              placeholder="Tên" />
          </div>
          <div className="form-group">
            <label>Trạng thái:</label>
            <select className="form-control" onChange={this.changeFrom} value={this.state.txtStatus} name="txtStatus">
              <option value={true}>Kích hoạt</option>
              <option value={false}>Ẩn</option>
            </select>
          </div>
          <div className="center">
            <button type="button" onClick={this.save} className="btn btn-warning"><i className="fas fa-plus"></i>&nbsp;Lưu lại</button>&nbsp;
                <button type="button" onClick={this.clean} className="btn btn-danger"><i className="fas fa-times"></i>&nbsp;Hủy bỏ</button>
          </div>
        </div>
      </div>

    )
  }
}
export default TaskForm;
