import React, { Component } from 'react';
import Search from './Search';
import Sort from './Sort';


class Control extends Component {
    onSearch = (keyword) =>{
        this.props.onSearch(keyword);
    }
    sortA_Z=(name,value)=>{
        this.props.sortA_Z(name,value);
    }
    sortZ_A=(name,value)=>{
        this.props.sortZ_A(name,value);
    }
    sortActive=(status,value)=>{
        this.props.sortActive(status,value);
    }
    sortHidden=(status,value)=>{
        this.props.sortHidden(status,value);
    }
    render() {
        return (
            <div className="row">
                <div className="col-6">
                    <Search onSearch={this.onSearch}/>
                </div>
                <div className="col-6">
                    <Sort 
                        sortA_Z={this.sortA_Z} 
                        sortZ_A={this.sortZ_A} 
                        sortActive={this.sortActive} 
                        sortHidden={this.sortHidden}
                    />
                </div>
            </div>
        )
    }
}
export default Control;
